<template>
  <div id='caic-cell'>
    <van-cell :value="content"
              :clickable="clickable"
              :is-link="isLink"
              :label='label'
              :border="border">
      <div slot="title"
           class="caic-cell__title">
        {{title}}
      </div>
      <div slot="right-icon"
           class="caic-cell__switch">
        <van-switch-cell v-model="checked"
                         :size="size"
                         :active-color="activeColor"
                         :inactive-color="inactiveColor"
                         :disabled='disabled'>
        </van-switch-cell>
      </div>
    </van-cell>
  </div>
</template>
<script>
import Vue from 'vue'
import {
  Cell,
  SwitchCell
} from 'vant'
Vue.use(Cell).use(SwitchCell)
export default {
  /**
   * -------------done---------------
   * title : String | Number 
   * content : Sting | Number  
   * label : Sting 
   * border : Boolean  --true
   * clickable : Boolean  --false
   * isLink : Boolean  --false
   * value: Boolean switch --false
   * disabled Boolean switch --false
   * size: String switch 20px
   * activeColor: String switch --#1989fa
   * inactiveColor:String switch --#fff
   * @change Function    checked 
   * -------------doing--------------
   *
   */
  name: 'caic-cell-switch',
  props: {
    value: {
      type: Boolean,
      required: false
    },
    title: {
      type: [String, Number]
    },
    content: {
      type: [String, Number]
    },
    label: {
      type: String
    },
    border: {
      type: Boolean,
      default: true
    },
    clickable: {
      type: Boolean,
      default: false
    },
    isLink: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    size: {
      type: String,
      default: '20px'
    },
    activeColor: {
      type: String,
      default: '#1989fa'
    },
    inactiveColor: {
      type: String,
      default: '#fff'
    }
  },
  data () {
    return {
      checked: this.value
    }
  },
  watch: {
    checked: function () {
      this.$emit('input', this.checked)
      this.$emit('change', this.checked)
    }
  }
}
</script>
<style lang="stylus" scoped>
@import '../../css/caic.styl'

#caic-cell
  .van-cell, .caic-cell__title
    changeFontSize(14px)

  .caic-cell__switch
    .van-switch-cell
      padding-top: 0
      padding-bottom: 0
      padding-right: 0
</style>



