<template>
  <div id="caic-birthday-picker">
    <van-field v-model="birthday"
               :readonly='true'
               :border="border"
               :label-align="labelAlign"
               :input-align="inputAlign"
               :disabled="disabled"
               placeholder=""
               @click="selBirthday">
      <div slot="label"
           :class="{'date-title':boldTitle}">
        <span>
          
        </span>
      </div>
      <div slot="right-icon">
        <slot name="right-icon">
          <span class=""
                @click="selBirthday">
            <svg class="icon svgIcon"
                 aria-hidden="true">
              <use xlink:href="#icon-date"></use>
            </svg>
          </span>
        </slot>
      </div>
    </van-field>
    <van-popup v-model="selBirthdayPopup"
               position="bottom">
      <van-datetime-picker v-model="defaultDate"
                           :type='type'
                           :min-date='minDate'
                           :max-date='maxDate'
                           @cancel='selDateCancel'
                           @confirm="selDateSure"
                           @change='selDateChange'
                           class="sel-date-picker">
      </van-datetime-picker>
    </van-popup>
  </div>
</template>
<script>
/**
 * ****************Done**********************
 * 1.defaultValue(Date) van-datetime-picker 
 * 2.blodTitle (Boolean) title 
 * 3.minDate (Date) van-datetime-picker 
 * 4.itemHeight Number  44
 * 5.dateFormatRuleString 
 * 6.typeString van-datetime-picker 
 * 7.baseDate (Date) ,
 * 8.confirmButtonText String  
 * 9.cancelButtonText String  
 * 10.visibleItemCount Number  5	
 * 11. @confirm() (function)  birthday-- formatedDate--, age--, birthDays--
 * 12. @cancel() Funccton 
 * 13. @change() Function  picker 
 * 
 * 14.border Boolean  true
 * 15.labelAlign String left center right left
 * 16.inputAlign String left center right left
 * 17.disabled Boolean  --false
 * 17.right-icon 
 * ***************doing********************
 * ?  ()
 * ***************except*******************
 * 
 */
import '../../font/iconfont.js'
// import { format, addYears } from 'date-fns'
import dayjs from 'dayjs'
import Vue from 'vue'
import {
  Field,
  Popup,
  DatetimePicker,
} from 'vant'
Vue.use(Field).use(Popup).use(DatetimePicker)

export default {
  name: 'caic-birthday-picker',
  props: {

    // 
    boldTitle: {
      type: Boolean,
      default: false
    },
    border: {
      type: Boolean,
      default: true
    },
    labelAlign: {
      type: String,
      default: 'left'
    },
    inputAlign: {
      type: String,
      default: 'left'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    // van-datetime-picker 
    defaultValue: {
      type: Date,
      default: function () {
        return new Date()
      }
    },

    // ,
    baseDate: {
      type: Date,
      default: function () {
        return new Date()
      }
    },

    // van-datetime-picker.
    minDate: {
      type: Date,
      default: function () {
        return dayjs().subtract(100, 'year').toDate();
      }
    },

    // van-datetime-picker.
    type: {
      type: String,
      default: 'date'
    },

    // 
    dateFormatRule: {
      type: String,
      default: 'YYYY-MM-DD'
    },

    itemHeight: {
      type: Number,
      default: 44
    },
    confirmButtonText: {
      type: String,
      default: ''
    },
    cancelButtonText: {
      type: String,
      default: ''
    },
    visibleItemCount: {
      type: Number,
      default: 5
    },
  },
  data () {
    return {
      birthday: null,
      birthdayObject: null,
      comparedDate: null,
      age: null,
      birthDays: null,
      selBirthdayPopup: false,
      defaultDate: this.defaultValue,
      maxDate: new Date()
    }
  },
  created () {
  },
  mounted () {
  },
  watch: {
    baseDate: function () {
      if (this.birthday !== null && this.birthday !== undefined && this.birthday !== '') {
        this.getAge()
        this.returnMsg(this.birthdayObject, this.birthday, this.age, this.birthDays)
      }
    }
  },
  methods: {
    blurFun: function () {
      this.blur()
    },
    // , + 
    getAge: function () {
      let birthY = this.birthdayObject.getFullYear()
      let birthM = this.birthdayObject.getMonth()
      let birthD = this.birthdayObject.getDate()

      let comparedY = this.baseDate.getFullYear()
      let comparedM = this.baseDate.getMonth()
      let comparedD = this.baseDate.getDate()

      this.age = comparedY - birthY - 1
      // +1
      if (birthM < comparedM || (birthM === comparedM && birthD <= comparedD)) {
        this.age += 1
      }
      // age>00
      // this.birthDays = (new Date(comparedY, comparedM, comparedD) - new Date(birthY + this.age, birthM, birthD)) / (24 * 60 * 60 * 1000)
      this.birthDays = (new Date(comparedY, comparedM, comparedD) - new Date(birthY, birthM, birthD)) / (24 * 60 * 60 * 1000)
      this.birthDays += 1
      // if (this.age === 0) {
      //   this.birthDays += 1
      // }
    },
    returnMsg: function (birthday, formatedBirthday, age, birthDays) {
      this.$emit('confirm', birthday, formatedBirthday, age, birthDays)
    },
    selBirthday: function () {
      this.selBirthdayPopup = true
    },
    selDateCancel: function () {
      this.selBirthdayPopup = false
      this.$emit('cancel')
    },
    selDateSure: function (value) {
      this.birthdayObject = value
      // this.birthday = format(value, this.dateFormatRule)
      this.birthday = dayjs(value).format(this.dateFormatRule)
      this.getAge()
      this.returnMsg(value, this.birthday, this.age, this.birthDays)
      this.selBirthdayPopup = false
    },
    selDateChange: function (picker) {
      this.$emit('change', picker)
    }
  }
}
</script>
<style lang="stylus" scoped>
@import '../../css/caic.styl'

#caic-birthday-picker
  .van-cell
    changeFontSize(14px)

  .date-title
    font-weight: 600
    changeFontSize(15px)

  .icon
    width: 1em
    height: 1em
    vertical-align: -0.15em
    fill: currentColor
    overflow: hidden
    changeFontSize(20px)
    color: #333
</style>



